
import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Plus, Package, TrendingUp, AlertCircle, Users } from 'lucide-react';
import { CreatePurchaseOrderModal } from './CreatePurchaseOrderModal';
import { Suspense, lazy } from 'react';
import { usePurchaseOrdersEnhanced } from '@/hooks/usePurchaseOrdersEnhanced';
import { transactionService } from '@/services/transactionService';
import { toast } from 'sonner';

const PurchaseOrderImportModal = lazy(() => import('./PurchaseOrderImportModal').then(module => ({ default: module.PurchaseOrderImportModal })));
const PurchaseOrderDetailModal = lazy(() => import('./PurchaseOrderDetailModal').then(module => ({ default: module.PurchaseOrderDetailModal })));
const PurchaseInvoicePreview = lazy(() => import('./PurchaseInvoicePreview').then(module => ({ default: module.PurchaseInvoicePreview })));

interface GroupedTransaction {
  poNumber: string;
  transactions: any[];
  totalAmount: number;
  itemCount: number;
  isImported: boolean;
  supplierName: string;
  clientPO?: string; // Add client_po field
}

const getStatusBadgeVariant = (status: string) => {
  switch (status?.toLowerCase()) {
    case 'completed':
      return 'default';
    case 'partial':
      return 'secondary';
    case 'pending':
      return 'outline';
    default:
      return 'secondary';
  }
};

const getPaymentStatusBadgeVariant = (status: string) => {
  switch (status?.toLowerCase()) {
    case 'paid':
      return 'default';
    case 'partial':
      return 'secondary';
    case 'unpaid':
      return 'destructive';
    default:
      return 'secondary';
  }
};

export const PurchaseOrdersTabEnhanced: React.FC = () => {
  const { 
    purchaseOrders, 
    loading, 
    error, 
    createPurchaseOrder, 
    getPurchaseOrderWithDetails,
    refetch 
  } = usePurchaseOrdersEnhanced();
  
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showImportModal, setShowImportModal] = useState(false);
  const [selectedPOId, setSelectedPOId] = useState<string | null>(null);
  const [previewPO, setPreviewPO] = useState<any | null>(null);
  const [populateLoading, setPopulateLoading] = useState(false);
  const [groupedTransactions, setGroupedTransactions] = useState<GroupedTransaction[]>([]);

  const handleViewDetails = (id: string) => {
    setSelectedPOId(id);
  };

  const handlePopulatePurchaseOrders = async () => {
    setPopulateLoading(true);
    try {
      // Get all transaction records with purchase_order_number
      const transactions = await transactionService.getAllTransactions();
      const purchaseTransactions = transactions.filter(t => 
        t.purchase_order_number && 
        t.purchase_order_number.trim() !== ''
      );

      if (purchaseTransactions.length === 0) {
        toast.info('No purchase transactions with PO numbers found');
        setPopulateLoading(false);
        return;
      }

      // Group transactions by purchase_order_number
      const grouped = purchaseTransactions.reduce((acc, transaction) => {
        const poNumber = transaction.purchase_order_number!;
        if (!acc[poNumber]) {
          acc[poNumber] = [];
        }
        acc[poNumber].push(transaction);
        return acc;
      }, {} as Record<string, typeof purchaseTransactions>);

      // Check which client_po values already exist
      const existingClientPOs = purchaseOrders.map(po => po.client_po).filter(Boolean);
      
      // Transform grouped data for the modal
      const groupedForModal = Object.entries(grouped).map(([poNumber, transactions]) => {
        const totalAmount = transactions.reduce((sum, t) => sum + (t.total_price || 0), 0);
        const supplierName = transactions[0].customer || poNumber;
        // Always use the original purchase order number for client_po
        const clientPO = poNumber;
        // Mark as imported if clientPO matches any existing client_po
        const isImported = existingClientPOs.includes(clientPO);
        return {
          poNumber,
          transactions,
          totalAmount,
          itemCount: transactions.length,
          isImported,
          supplierName,
          clientPO
        };
      });

      setGroupedTransactions(groupedForModal);
      setShowImportModal(true);
    } catch (error) {
      console.error('Error fetching transaction data:', error);
      toast.error('Failed to fetch transaction data');
    } finally {
      setPopulateLoading(false);
    }
  };

  const handleImportPurchaseOrders = async (selectedPONumbers: string[]) => {
    try {
      let createdCount = 0;
      
      for (const poNumber of selectedPONumbers) {
        const group = groupedTransactions.find(g => g.poNumber === poNumber);
        if (!group || group.isImported) continue;

        // Prepare items from transactions with validation
        const items = group.transactions
          .filter(t => t.model && t.quantity > 0 && t.unit_price > 0)
          .map(t => ({
            product_id: t.product_id || null,
            model: t.model,
            quantity: parseInt(t.quantity.toString()) || 1,
            unit_price: parseFloat(t.unit_price.toString()) || 0
          }));

        console.log(`Creating PO for ${poNumber} with items:`, items);

        if (items.length === 0) {
          console.warn(`Skipping PO ${poNumber} - no valid items found`);
          toast.warning(`Skipped PO ${poNumber} - no valid items found`);
          continue;
        }

        try {
          await createPurchaseOrder({
            supplier_name: group.supplierName,
            status: 'completed',
            payment_status: 'unpaid',
            notes: `Imported from transaction records (PO: ${poNumber})`,
            client_po: group.clientPO, // Add client_po to the creation
            items
          });
          createdCount++;
        } catch (error) {
          console.error(`Failed to create PO for ${poNumber}:`, error);
          toast.error(`Failed to import PO: ${poNumber}`);
        }
      }

      if (createdCount > 0) {
        toast.success(`Successfully imported ${createdCount} purchase order(s)`);
        refetch();
        setShowImportModal(false);
      }
    } catch (error) {
      console.error('Error importing purchase orders:', error);
      toast.error('Failed to import purchase orders');
    }
  };

  const handlePreviewInvoice = async (id: string) => {
    try {
      const poDetails = await getPurchaseOrderWithDetails(id);
      if (poDetails) {
        setPreviewPO(poDetails);
      } else {
        toast.error('Purchase order not found');
      }
    } catch (error) {
      console.error('Error fetching PO details for preview:', error);
      toast.error('Failed to load purchase order details');
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-2"></div>
          <p className="text-gray-600">Loading purchase orders...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="text-center text-red-600">
          <AlertCircle className="h-8 w-8 mx-auto mb-2" />
          <p>Error loading purchase orders: {error}</p>
        </div>
      </div>
    );
  }

  const completedOrders = purchaseOrders.filter(po => po.status === 'completed');
  const pendingOrders = purchaseOrders.filter(po => po.status === 'pending');
  const partialOrders = purchaseOrders.filter(po => po.status === 'partial');

  return (
    <div className="space-y-6">
      {/* Header with Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center space-x-2">
              <Package className="h-5 w-5 text-blue-600" />
              <div>
                <p className="text-sm text-gray-600">Total Orders</p>
                <p className="text-xl font-bold">{purchaseOrders.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center space-x-2">
              <TrendingUp className="h-5 w-5 text-green-600" />
              <div>
                <p className="text-sm text-gray-600">Completed</p>
                <p className="text-xl font-bold text-green-600">{completedOrders.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4">
            <div className="flex items-center space-x-2">
              <AlertCircle className="h-5 w-5 text-yellow-600" />
              <div>
                <p className="text-sm text-gray-600">Pending</p>
                <p className="text-xl font-bold text-yellow-600">{pendingOrders.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-4">
            <div className="flex items-center space-x-2">
              <Users className="h-5 w-5 text-purple-600" />
              <div>
                <p className="text-sm text-gray-600">Partial</p>
                <p className="text-xl font-bold text-purple-600">{partialOrders.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Main Content */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="flex items-center gap-2">
              <Package className="h-5 w-5" />
              Purchase Orders Management
            </CardTitle>
            <div className="flex gap-2">
              <Button 
                onClick={handlePopulatePurchaseOrders}
                variant="outline"
                disabled={populateLoading}
                className="flex items-center gap-2"
              >
                <Package className="h-4 w-4" />
                {populateLoading ? 'Populating...' : 'Populate'}
              </Button>
              <Button onClick={() => setShowCreateModal(true)} className="flex items-center gap-2">
                <Plus className="h-4 w-4" />
                New Purchase Order
              </Button>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <Tabs defaultValue="all" className="w-full">
            <TabsList className="grid w-full grid-cols-4">
              <TabsTrigger value="all">All Orders</TabsTrigger>
              <TabsTrigger value="pending">Pending</TabsTrigger>
              <TabsTrigger value="partial">Partial</TabsTrigger>
              <TabsTrigger value="completed">Completed</TabsTrigger>
            </TabsList>

            <TabsContent value="all" className="mt-6">
              <PurchaseOrderTable 
                orders={purchaseOrders} 
                onViewDetails={handleViewDetails}
                onPreviewInvoice={handlePreviewInvoice}
              />
            </TabsContent>

            <TabsContent value="pending" className="mt-6">
              <PurchaseOrderTable 
                orders={pendingOrders} 
                onViewDetails={handleViewDetails}
                onPreviewInvoice={handlePreviewInvoice}
              />
            </TabsContent>

            <TabsContent value="partial" className="mt-6">
              <PurchaseOrderTable 
                orders={partialOrders} 
                onViewDetails={handleViewDetails}
                onPreviewInvoice={handlePreviewInvoice}
              />
            </TabsContent>

            <TabsContent value="completed" className="mt-6">
              <PurchaseOrderTable 
                orders={completedOrders} 
                onViewDetails={handleViewDetails}
                onPreviewInvoice={handlePreviewInvoice}
              />
            </TabsContent>
          </Tabs>
        </CardContent>
      </Card>

      {/* Modals */}
      {showCreateModal && (
        <CreatePurchaseOrderModal
          onClose={() => setShowCreateModal(false)}
          onSuccess={() => {
            setShowCreateModal(false);
            refetch();
          }}
        />
      )}
      
      <Suspense fallback={null}>
        {showImportModal && (
          <PurchaseOrderImportModal
            isOpen={showImportModal}
            onClose={() => setShowImportModal(false)}
            groupedTransactions={groupedTransactions}
            onImport={handleImportPurchaseOrders}
            isLoading={populateLoading}
          />
        )}
        {selectedPOId && (
          <PurchaseOrderDetailModal
            purchaseOrderId={selectedPOId}
            onClose={() => setSelectedPOId(null)}
          />
        )}
        {previewPO && (
          <PurchaseInvoicePreview
            isOpen={!!previewPO}
            onClose={() => setPreviewPO(null)}
            purchaseOrder={previewPO}
          />
        )}
      </Suspense>
    </div>
  );
};

interface PurchaseOrderTableProps {
  orders: any[];
  onViewDetails: (id: string) => void;
  onPreviewInvoice?: (id: string) => void;
}

const PurchaseOrderTable: React.FC<PurchaseOrderTableProps> = ({ orders, onViewDetails, onPreviewInvoice }) => {
  if (orders.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
        <Package className="h-12 w-12 mx-auto mb-4 text-gray-300" />
        <p>No purchase orders found</p>
      </div>
    );
  }

  return (
    <div className="border rounded-lg overflow-hidden">
      <div className="overflow-x-auto">
        <table className="w-full text-sm">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-4 py-3 text-left font-medium">Date</th>
              <th className="px-4 py-3 text-left font-medium">Supplier</th>
              <th className="px-4 py-3 text-left font-medium">Client PO</th>
              <th className="px-4 py-3 text-left font-medium">Status</th>
              <th className="px-4 py-3 text-left font-medium">Payment</th>
              <th className="px-4 py-3 text-left font-medium">Notes</th>
              <th className="px-4 py-3 text-left font-medium">Actions</th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order) => (
              <tr key={order.id} className="border-t hover:bg-gray-50">
                <td className="px-4 py-3">
                  {new Date(order.created_at).toLocaleDateString()}
                </td>
                <td className="px-4 py-3 font-medium">
                  {order.supplier_name || 'N/A'}
                </td>
                <td className="px-4 py-3">
                  {order.client_po || '-'}
                </td>
                <td className="px-4 py-3">
                  <Badge variant={getStatusBadgeVariant(order.status)}>
                    {order.status}
                  </Badge>
                </td>
                <td className="px-4 py-3">
                  <Badge variant={getPaymentStatusBadgeVariant(order.payment_status)}>
                    {order.payment_status}
                  </Badge>
                </td>
                <td className="px-4 py-3 max-w-xs truncate">
                  {order.notes || '-'}
                </td>
                <td className="px-4 py-3">
                  <div className="flex gap-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => onViewDetails(order.id)}
                    >
                      View Details
                    </Button>
                    {onPreviewInvoice && (
                      <Button
                        variant="secondary"
                        size="sm"
                        onClick={() => onPreviewInvoice(order.id)}
                      >
                        Preview
                      </Button>
                    )}
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};
