
import { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Plus, Minus } from 'lucide-react';
import { useDeliveries } from '@/hooks/useDeliveries';
import { usePurchaseOrdersEnhanced } from '@/hooks/usePurchaseOrdersEnhanced';
import { CreateDeliveryData } from '@/types/purchaseOrder';

interface CreateDeliveryModalProps {
  onClose: () => void;
  onSuccess?: () => void;
  purchaseOrderId?: string;
}

interface DeliveryItemForm {
  product_id?: string;
  quantity_delivered: number;
}

export const CreateDeliveryModal = ({ onClose, onSuccess, purchaseOrderId }: CreateDeliveryModalProps) => {
  const { createDelivery } = useDeliveries();
  const { purchaseOrders } = usePurchaseOrdersEnhanced();
  const [loading, setLoading] = useState(false);
  
  const [formData, setFormData] = useState({
    purchase_order_id: purchaseOrderId || 'none',
    delivery_date: new Date().toISOString().split('T')[0],
    delivery_receipt_number: '',
    notes: ''
  });
  
  const [items, setItems] = useState<DeliveryItemForm[]>([
    { quantity_delivered: 1 }
  ]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      const deliveryData: CreateDeliveryData = {
        purchase_order_id: formData.purchase_order_id && formData.purchase_order_id !== 'none' 
          ? formData.purchase_order_id 
          : undefined,
        delivery_date: formData.delivery_date,
        delivery_receipt_number: formData.delivery_receipt_number || undefined,
        notes: formData.notes || undefined,
        items: items.filter(item => item.quantity_delivered > 0)
      };

      await createDelivery(deliveryData);
      onSuccess?.();
      onClose();
    } catch (error) {
      console.error('Error creating delivery:', error);
    } finally {
      setLoading(false);
    }
  };

  const updateItem = (index: number, field: keyof DeliveryItemForm, value: any) => {
    setItems(prev => prev.map((item, i) => 
      i === index ? { ...item, [field]: value } : item
    ));
  };

  const addItem = () => {
    setItems(prev => [...prev, { quantity_delivered: 1 }]);
  };

  const removeItem = (index: number) => {
    setItems(prev => prev.filter((_, i) => i !== index));
  };

  return (
    <Dialog open onOpenChange={onClose}>
      <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Record Delivery</DialogTitle>
        </DialogHeader>

        <form
          onSubmit={handleSubmit}
          className="flex-1 overflow-y-auto p-4 space-y-6"
          style={{ paddingBottom: "6rem" }} // Adjust if your sticky bar is taller/shorter
        >
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="purchase_order_id">Purchase Order (Optional)</Label>
              <Select 
                value={formData.purchase_order_id} 
                onValueChange={(value) => setFormData(prev => ({ ...prev, purchase_order_id: value }))}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Select PO (optional)" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="none">No PO Selected</SelectItem>
                  {purchaseOrders.map(po => (
                    <SelectItem key={po.id} value={po.id}>
                      PO #{po.id.slice(0, 8)} - {po.supplier_name || 'Unknown Supplier'}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label htmlFor="delivery_date">Delivery Date</Label>
              <Input
                id="delivery_date"
                type="date"
                value={formData.delivery_date}
                onChange={(e) => setFormData(prev => ({ ...prev, delivery_date: e.target.value }))}
                required
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="delivery_receipt_number">Delivery Receipt Number</Label>
            <Input
              id="delivery_receipt_number"
              placeholder="DR-001 (optional)"
              value={formData.delivery_receipt_number}
              onChange={(e) => setFormData(prev => ({ ...prev, delivery_receipt_number: e.target.value }))}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="notes">Notes</Label>
            <Textarea
              id="notes"
              placeholder="Delivery notes (optional)"
              value={formData.notes}
              onChange={(e) => setFormData(prev => ({ ...prev, notes: e.target.value }))}
            />
          </div>

          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <Label>Delivered Items</Label>
              <Button type="button" size="sm" onClick={addItem}>
                <Plus size={14} className="mr-1" />
                Add Item
              </Button>
            </div>

            {items.map((item, index) => (
              <div key={index} className="border rounded p-3 space-y-3">
                <div className="flex justify-between items-start">
                  <h4 className="font-medium">Item {index + 1}</h4>
                  {items.length > 1 && (
                    <Button
                      type="button"
                      variant="outline"
                      size="sm"
                      onClick={() => removeItem(index)}
                    >
                      <Minus size={14} />
                    </Button>
                  )}
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  <div className="space-y-2">
                    <Label>Product ID (Optional)</Label>
                    <Input
                      placeholder="Product identifier"
                      value={item.product_id || ''}
                      onChange={(e) => updateItem(index, 'product_id', e.target.value)}
                    />
                  </div>
                  <div className="space-y-2">
                    <Label>Quantity Delivered</Label>
                    <Input
                      type="number"
                      min="1"
                      value={item.quantity_delivered}
                      onChange={(e) => updateItem(index, 'quantity_delivered', parseInt(e.target.value) || 1)}
                      required
                    />
                  </div>
                </div>
              </div>
            ))}
          </div>
        </form>
        <div
          className="fixed bottom-0 left-0 w-full z-[10001] bg-white border-t border-purple-200 p-4 flex flex-col sm:flex-row gap-2 sm:gap-4"
          style={{ marginBottom: "4rem" }} // Adjust if your sticky bar is taller/shorter
        >
          <Button type="button" variant="outline" onClick={onClose} className="w-full sm:w-auto">
            Cancel
          </Button>
          <Button type="submit" form="delivery-form" disabled={loading} className="w-full sm:w-auto">
            {loading ? 'Recording...' : 'Record Delivery'}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};
